      <label for="id">IMBD ID</label>
      <input id="id" type="text" v-model="imdbId" />
      <button @click="getNewMovieData(imdbId)">Add</button>
      <br />
      <h3>Get Imdb Ids</h3>
      <label for="imdbYear">year</label>
